:root {
  --blue: #00acdc;
}

html {
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

.header {
  justify-content: space-between;
  align-items: center;
  margin: 0;
  padding: 0 50px;
  display: flex;
}

.header .nav__item {
  margin-left: 20px;
  display: flex;
}

.nav {
  justify-content: flex-end;
  padding-left: 0;
  display: flex;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__link {
  text-align: center;
  text-transform: uppercase;
  color: #000;
  height: 60px;
  line-height: 60px;
  text-decoration: none;
}

.nav__link:hover {
  color: var(--blue);
}

ul {
  margin: 0;
  padding-left: 0;
  display: flex;
}

.is-active {
  color: var(--blue);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--blue);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}
